import React from 'react';
const protobuf = require("protobufjs");
const shortid = require('shortid');
const ProtoConvert = require('../ProtoConvert')

const { List, Map } = require('immutable');

class MessageBuilderScaffold extends React.Component {
  render() {
    if (this.props.messageToBuild === undefined) {
      return null;
    }
    return (
      <React.Fragment>
        <h1>
          {this.props.messageToBuild.name} builder
        </h1>
        <MessageFieldsBuilder
          path={List()}
          fields={this.props.messageToBuild.fields}
          values={this.props.message}
          callbacks={this.props.callbacks} />
        <Output
          builtMessage={this.props.builtMessage}
          buildMessageUpToDate={this.props.buildMessageUpToDate}
          callbacks={this.props.callbacks} />
      </React.Fragment>
    );
  }
}

class Output extends React.Component {
  handleChangeOutputFormat(event) {
    this.props.callbacks.onSetOutputFormat(event.target.value);
  }

  handleBuild(type) {
    this.props.callbacks.onBuild(type);
  }

  render() {
    const handleChangeOutputFormat = this.handleChangeOutputFormat.bind(this);
    const handleBuild = (type) => () => this.handleBuild(type);
    return (
      <React.Fragment>
        <select onChange={handleChangeOutputFormat}>
          {ProtoConvert.converterFormats.map(key =>
            <option key={key} value={key}>
              {ProtoConvert.converterName(key)}
            </option>
          )}
        </select>
        <button onClick={handleBuild('save')}> Save as file </button>
        <br/>
        <textarea
          value={this.props.builtMessage}
          cols="200"
          rows="3"
          readOnly
          disabled={!this.props.buildMessageUpToDate} />
      </React.Fragment>
    );
  }
}

class MessageFieldsBuilder extends React.Component {
  render() {
    return (
      <ol>
        {Object.entries(this.props.fields)
         .map(
          ([fieldName, field]) =>
            <li key={fieldName} value={field.id}>
              <b>{field.type}</b> - {field.name}
              <MessageFieldValuesBuilder
                path={this.props.path.concat([field.id])}
                field={field}
                valueEntries={this.props.values.get(field.id) || List()}
                callbacks={this.props.callbacks} />
            </li>
        )}
      </ol>
    );
  }
}


class MessageFieldValuesBuilder extends React.Component{
  handleSet() {
    this.props.callbacks.onAdd(
      this.props.path,
      Map({
        'id': shortid.generate(),
        'value': ProtoConvert.defaultValueForField(this.props.field)
      }));
  }

  handleDelete(index) {
    this.props.callbacks.onDelete(this.props.path.concat([index]))
  }

  handleDuplicate(valueEntry) {
    this.props.callbacks.onAdd(
      this.props.path,
      valueEntry.set('id', shortid.generate())
    );
  }

  handleMoveUp(index) {
    this.props.callbacks.onMove(this.props.path, index, index - 1);
  }

  handleMoveDown(index) {
    this.props.callbacks.onMove(this.props.path, index, index + 1);
  }

  render() {
    const showSet = this.props.valueEntries.size === 0;
    const showAdd = this.props.field.repeated;
    const showDuplicate = this.props.field.repeated;
    const showMove = this.props.field.repeated;

    return (
      <ul>
        {this.props.valueEntries.map((valueEntry, index) => {
          const enableMoveDown = index !== this.props.valueEntries.size - 1;
          const enableMoveUp = index !== 0;
          return <li key={valueEntry.get('id')} >
            <button onClick={() => this.handleDelete(index)}> - </button>
            <button
              onClick={() => this.handleDuplicate(valueEntry)}
              hidden={!showDuplicate}>
              *
            </button>
            <button
              onClick={() => this.handleMoveDown(index)}
              hidden={!showMove}
              disabled={!enableMoveDown}>
              v
            </button>
            <button
              onClick={() => this.handleMoveUp(index)}
              hidden={!showMove}
              disabled={!enableMoveUp}>
              ^
            </button>
            <MessageFieldInput
              path={this.props.path.concat([index, 'value'])}
              field={this.props.field}
              value={valueEntry.get('value')}
              callbacks={this.props.callbacks} />
          </li>
        })}
        {(showAdd || showSet) &&
          <li>
            <button onClick={() => this.handleSet()}> {(showAdd) ? 'add' : 'set'} </button>
          </li>
        }
      </ul>
    );
  }
}

class MessageFieldInput extends React.Component {
  render() {
    const fieldType = this.props.field.type;
    switch (fieldType) {
      case "string":
        return <MessageTextInput
          path={this.props.path}
          field={this.props.field}
          value={this.props.value}
          callbacks={this.props.callbacks} />
      case "bytes":
        return <MessageBytesInput
          path={this.props.path}
          field={this.props.field}
          value={this.props.value}
          callbacks={this.props.callbacks} />
      case "int32":
      case "int64":
      case "sint32":
      case "sint64":
      case "uint32":
      case "uint64":
      case "fixed32":
      case "fixed64":
      case "float":
      case "double":
        return <MessageIntInput
          path={this.props.path}
          field={this.props.field}
          value={this.props.value}
          callbacks={this.props.callbacks} />
      case "bool":
        return <MessageBoolInput
          path={this.props.path}
          field={this.props.field}
          value={this.props.value}
          callbacks={this.props.callbacks} />
      default:
        break;
    }
    const resolvedType = this.props.field.resolvedType;
    if (resolvedType.constructor === protobuf.Enum) {
      return <MessageEnumInput
        path={this.props.path}
        field={this.props.field}
        type={resolvedType}
        value={this.props.value}
        callbacks={this.props.callbacks} />
    } else if (resolvedType.constructor === protobuf.Type) {
      return <MessageFieldsBuilder
        path={this.props.path}
        fields={resolvedType.fields}
        values={this.props.value}
        callbacks={this.props.callbacks} />
    }
    console.log("Unhandled input type", this.props.field.type);
    return <div/>
  }
}

class MessageTextInput extends React.Component {
  handleChange(event) {
    // console.log('MessageTextInput change', this.props.path, event.target.value);
    this.props.callbacks.onSet(this.props.path, event.target.value);
  }

  render() {
    return <textarea onChange={(event) => this.handleChange(event)} value={this.props.value} />
  }
}

class MessageBytesInput extends React.Component {
  handleChange(event) {
    // console.log("MessageBytesInput change", this.props.path, event.target.value);
    this.props.callbacks.onSet(this.props.path, event.target.value);
  }

  render() {
    return <textarea onChange={(event) => this.handleChange(event)} value={this.props.value} />
  }

}

class MessageIntInput extends React.Component {
  handleChange(event) {
    // console.log('MessageIntInput change', this.props.path, event.target.value);
    this.props.callbacks.onSet(this.props.path, event.target.value);
  }

  render() {
    return <input type="text" onChange={(event) => this.handleChange(event)} value={this.props.value} />
  }
}

class MessageBoolInput extends React.Component {
  handleChange(event) {
    console.log('MessageBoolInput', this.props.path, event.target.checked);
    this.props.callbacks.onSet(this.props.path, event.target.checked);
  }

  render() {
    return (
      <input
        type="checkbox"
        checked={this.props.value}
        onChange={(event) => this.handleChange(event)} />
    )
  }
}

class MessageEnumInput extends React.Component {
  handleChange(event) {
    const value = parseInt(event.target.value);
    console.log('MessageEnumInput.change', this.props.path, typeof(value), value);
    this.props.callbacks.onSet(this.props.path, value);
  }

  render() {
    const keys = List(Object.keys(this.props.type.valuesById)).sort();
    return (
      <select defaultValue={this.props.value} onChange={(event) => this.handleChange(event)}>
        {keys.map(key => (
          <option value={key} key={key}>
            {this.props.type.valuesById[key]} = {key}
          </option>
        )).toJS()}
      </select>
    )
  }
}

export default MessageBuilderScaffold;
