const protobuf = require("protobufjs");
const { List, Map } = require('immutable');

const converters = {
  'binary': ['Binary', messageToBinary],
  'binary-hex': ['Binary (hex)', messageToBinaryHex],
  'binary-b64': ['Binary (base64)', messageToBinaryBase64],
  'json': ['Json', messageToJson],
  'json-camel': ['Json (camelCase)', messageToJsonCamelCase],
  'json-id': ['Json (field-IDs)', messageToJsonIds],
};

const converterFormats = Object.keys(converters).sort();

function converterName(format) {
  return converters[format][0];
}

function convert(format, messageDescriptor, message) {
  return converters[format][1](messageDescriptor, message);
}

function messageToBinary(messageDescriptor, message) {
  const protoMessage = messageToProto(messageDescriptor, message);
  return messageDescriptor.encode(protoMessage).finish();
}

function messageToBinaryHex(messageDescriptor, message) {
  const binaryMessage = messageToBinary(messageDescriptor, message);
  const hex = Array.from(binaryMessage).map((b) =>
    ((b < 0x10) ? '0' : '') + b.toString(16)
  );
  return hex.join('').toUpperCase();
}

function messageToBinaryBase64(messageDescriptor, message) {
  const binaryMessage = messageToBinary(messageDescriptor, message);
  return protobuf.util.base64.encode(binaryMessage, 0, binaryMessage.length);
}

function messageToJson(messageDescriptor, message) {
  const protoMessage = messageToProto(messageDescriptor, message);
  return JSON.stringify(protoMessage);
}

function messageToJsonCamelCase(messageDescriptor, message) {
  const protoMessage = messageToProto(messageDescriptor, message);
  const camelCaseMessage = replaceFieldNames(
    messageDescriptor,
    protoMessage,
    (fieldName) => protobuf.util.camelCase(fieldName)
  );
  return JSON.stringify(camelCaseMessage);
}

function messageToJsonIds(messageDescriptor, message) {
  const protoMessage = messageToProto(messageDescriptor, message);
  const pbJsonMessage = replaceFieldNames(
    messageDescriptor, protoMessage, (fieldName, field) => field.id);
  return JSON.stringify(pbJsonMessage);
}

function replaceFieldNames(messageDescriptor, protoMessage, replacementFunc) {
  return Object.fromEntries(Object.entries(protoMessage).map(([k, v]) => {
    const field = messageDescriptor.fields[k];
    const newName = replacementFunc(k, field);
    if (field.resolvedType && field.resolvedType.constructor === protobuf.Type) {
      if (field.repeated) {
        return [
          newName,
          v.map((value) => replaceFieldNames(field.resolvedType, value, replacementFunc))
        ];
      } else {
        return [newName, replaceFieldNames(field.resolvedType, v, replacementFunc)];
      }
    } else {
      return [newName, v];
    }
  }));
}

function messageToProto(messageDescriptor, message) {
  const messageObject = messageToProtoInternal(messageDescriptor, message);
  return messageDescriptor.fromObject(messageObject);
}

function messageToProtoInternal(messageDescriptor, message) {
  return message.mapEntries(([key, valueEntries]) => {
    const field = messageDescriptor.fieldsById[key];
    return [field.name, valueEntriesToProto(field, valueEntries)];
  }).toObject();
}

function valueEntriesToProto(field, valueEntries) {
  if (field.repeated) {
    return valueEntries.map(valueEntry =>
      valueToProto(field, valueEntry.get('value'))
    ).toArray();
  } else if (valueEntries.size === 1) {
    return valueToProto(field, valueEntries.get(0).get('value'));
  } else {
    return null;
  }
}

function valueToProto(field, value) {
  switch (field.type) {
    case "string":
      return value;
    case "bytes":
      return value;
    case "int32":
    case "int64":
    case "sint32":
    case "sint64":
    case "uint32":
    case "uint64":
    case "fixed32":
    case "fixed64":
    case "float":
    case "double":
      return value;
    case "bool":
      return value;
    default:
      break;
  }
  const resolvedType = field.resolvedType;
  if (resolvedType === null) {
    console.log("Unhandled value type", field);
    return "";
  }
  if (resolvedType.constructor === protobuf.Enum) {
    return value;
  } else if (resolvedType.constructor === protobuf.Type) {
    return messageToProtoInternal(resolvedType, value);
  }
  console.log("Unhandled value type", field);
  return "";
}

function defaultValueForField(field) {
  switch (field.type) {
    case "string":
    case "bytes":
      return "";
    case "int32":
    case "int64":
    case "sint32":
    case "sint64":
    case "uint32":
    case "uint64":
    case "fixed32":
    case "fixed64":
    case "float":
    case "double":
      return "0"
    case "bool":
      return false;
    default:
      break;
  }
  const resolvedType = field.resolvedType;
  if (resolvedType === null) {
    console.log("Unhandled default type", field);
    return "";
  }
  if (resolvedType.constructor === protobuf.Enum) {
    return parseInt(List(Object.keys(resolvedType.valuesById)).sort().first(), 10);
  } else if (resolvedType.constructor === protobuf.Type) {
    return Map();
  }
  console.log("Unhandled default type", field);
  return "";
}

export { converterFormats, converterName, convert, defaultValueForField, messageToProto };
