import React from 'react';
import LocalStorage from '../LocalStorage'
import MessageBuilderScaffold from './MessageBuilder';
import ProtoLibrary from './ProtoLibrary';
const ProtoConvert = require('../ProtoConvert')
const protobuf = require("protobufjs");
const FileSaver = require("file-saver");
const { List, Map } = require('immutable');

/*
window.localStorage.setItem('stored_protos', JSON.stringify({
  'awesome.proto': {
    'contents': '// awesome.proto\npackage awesomepackage;\nsyntax = "proto3";\nmessage AwesomeMessage {\n  string awesome_field = 2;\n}'
  }
}))
/**/

function parseStoredProto(proto, filename) {
  console.log('protobuf', protobuf);
  return proto.updateIn(
    ['contents'],
    (contents) => {
      protobuf.parse.filename = filename;
      const parsedProto = protobuf.parse(contents, { keepCase: true });
      parsedProto.root.resolveAll();
      return parsedProto;
    }
  );
}

class Main extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      rawStoredProtos: Map(),
      storedProtos: Map(),
      message: Map(),
      outputFormat: 'binary',
      builtMessage: '',
    }
  }

  handleAddProto(fileName, protoContent) {
    const parsedContent = protobuf.parse(protoContent, { keepCase: true });
    this.setState((state) => ({
      rawStoredProtos: state.rawStoredProtos.setIn([fileName], Map({contents: protoContent})),
      storedProtos: state.storedProtos.setIn([fileName], Map({contents: parsedContent})),
    }));
  }

  handleRemoveProto(fileName) {
    // console.log('handleRemoveProto', this.state.rawStoredProtos.toString());
    this.setState((state) => ({
      rawStoredProtos: state.rawStoredProtos.delete(fileName),
      storedProtos: state.storedProtos.delete(fileName),
    }));
  }

  handleSelectMessageToBuild(messageToBuild) {
    this.setState({
      messageToBuild,
      message: Map(),
      builtMessage: '',
      messageBuildTimer: null,
    });
  }

  handleSetOutputFormat(outputFormat) {
    this.setState({
      outputFormat,
      builtMessage: ProtoConvert.convert(
        outputFormat,
        this.state.messageToBuild,
        this.state.message),
    });
  }

  handleBuildMessage(kind) {
    console.log('Will', kind, 'as', this.state.outputFormat);

    const data = ProtoConvert.convert(
      this.state.outputFormat,
      this.state.messageToBuild,
      this.state.message);
    // console.log('data', data);
    console.log('handleBuildMessage', this.refs);

    if (kind === 'save') {
      const blob = new Blob([data], {type: 'application/octet-stream'});
      FileSaver.saveAs(blob, this.state.messageToBuild.name + '.pbdata');
    } else {
      console.log('Unhandled build kind:', kind);
    }
  }

  handleAddToMessage(path, valueEntry) {
    // console.log('handleAddToMessage', this.state.message, path.toString(), valueEntry);
    this.setState((state) => ({
      message: state.message.updateIn(
        path,
        (list = List()) => list.push(valueEntry)
      ),
      messageBuildTimer: this.scheduleBuildMessage(state.messageBuildTimer),
    }));
  }

  handleDeleteFromMessage(path) {
    // console.log('handleDeleteFromMessage', this.state.message, path.toString());
    this.setState((state) => ({
      message: state.message.deleteIn(path),
      messageBuildTimer: this.scheduleBuildMessage(state.messageBuildTimer),
    }));
  }

  handleSetInMessage(path, valueEntry) {
    // console.log('handleSetInMessage', this.state.message, path.toString(), valueEntry);
    this.setState((state) => ({
      message: state.message.setIn(path, valueEntry),
      messageBuildTimer: this.scheduleBuildMessage(state.messageBuildTimer),
    }));
  }

  handleMoveInMessage(path, fromIndex, toIndex) {
    this.setState((state) => ({
      message: state.message.updateIn(
        path,
        (list = List()) => {
          const movedEntry = list.get(fromIndex);
          return list.delete(fromIndex).insert(toIndex, movedEntry);
        }
      ),
      messageBuildTimer: this.scheduleBuildMessage(state.messageBuildTimer),
    }));
  }

  componentDidMount() {
    const rawStoredProtos = LocalStorage.loadProtosFromStorage();
    const storedProtos = rawStoredProtos.map(parseStoredProto);

    console.log("storedProtos", storedProtos);
    this.setState({
      rawStoredProtos,
      storedProtos
    });
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState.rawStoredProtos !== this.state.rawStoredProtos) {
      LocalStorage.saveProtosToStorage(this.state.rawStoredProtos);
    }
  }

  scheduleBuildMessage(prevTimeout) {
    clearTimeout(prevTimeout);
    return setTimeout(() =>
      this.setState({
        builtMessage: ProtoConvert.convert(
          this.state.outputFormat,
          this.state.messageToBuild,
          this.state.message),
        messageBuildTimer: null
      }),
      500
    );
  }

  fullMessageDescriptorName(messageDescriptor) {
    if (!messageDescriptor) {
      return "";
    }
    var name = messageDescriptor.name;
    while (messageDescriptor.parent && messageDescriptor.parent.name) {
      name = messageDescriptor.parent.name + '.' + name;
      messageDescriptor = messageDescriptor.parent;
    }
    name = messageDescriptor.filename + ':' + name;
    return name;
  }

  render() {
    const libraryCallbacks = {
      onAddProto: this.handleAddProto.bind(this),
      onRemoveProto: this.handleRemoveProto.bind(this),
      onSelectMessageToBuild: this.handleSelectMessageToBuild.bind(this),
    };
    const messageBuilderCallbacks = {
      onAdd: this.handleAddToMessage.bind(this),
      onDelete: this.handleDeleteFromMessage.bind(this),
      onSet: this.handleSetInMessage.bind(this),
      onMove: this.handleMoveInMessage.bind(this),
      onSetOutputFormat: this.handleSetOutputFormat.bind(this),
      onBuild: this.handleBuildMessage.bind(this),
    };

    return (
      <div className="protool">
        <div className="message-builder">
          <MessageBuilderScaffold
            key={this.fullMessageDescriptorName(this.state.messageToBuild)}
            messageToBuild={this.state.messageToBuild}
            message={this.state.message}
            builtMessage={this.state.builtMessage}
            buildMessageUpToDate={this.state.messageBuildTimer === null}
            callbacks={messageBuilderCallbacks} />
        </div>
        <div className="proto-library">
          <ProtoLibrary
            protoFiles={this.state.storedProtos}
            callbacks={libraryCallbacks} />
        </div>
      </div>
    );
  }
}

export default Main;
