const { fromJS } = require('immutable');

function loadProtosFromStorage() {
  const rawStoredProtos = fromJS(JSON.parse(window.localStorage.getItem('stored_protos') || "{}"));

  console.log('rawStoredProtos', rawStoredProtos);

  return rawStoredProtos;
}

function saveProtosToStorage(rawStoredProtos) {
  console.log('SAVING:', rawStoredProtos.toJSON());
  window.localStorage.setItem('stored_protos', JSON.stringify(rawStoredProtos.toJSON()));
}

export default { loadProtosFromStorage, saveProtosToStorage }
