import React from 'react';
const protobuf = require("protobufjs");

class ProtoNamespace extends React.Component {
  render() {
    const nested = this.props.namespace['nested'] || {};
    const entries = Object.entries(nested);
    const namespaces = entries.filter(([k, v]) => v.constructor === protobuf.Namespace);
    const types = entries.filter(([k, v]) => v.constructor === protobuf.Type);

    return (
      <React.Fragment>
        <button
          onClick={() => this.props.callbacks.onRemoveProto(this.props.name)}
          hidden={!this.props.removable}>
           -
        </button>
        {this.props.name}
        <button
          onClick={() => this.props.callbacks.onSelectMessageToBuild(this.props.namespace)}
          hidden={!this.props.buildable}>
          build
        </button>
        <ul>
          {namespaces.map(([k ,v]) =>
            <li key={k} >
              <ProtoNamespace
                file={this.props.file}
                name={k}
                namespace={v}
                callbacks={this.props.callbacks} />
            </li>
          )}
          {types.map(([k, v]) =>
            <li key={k}>
              <ProtoMessage
                file={this.props.file}
                message={v}
                callbacks={this.props.callbacks} />
            </li>
           )}
        </ul>
      </React.Fragment>
    );
  }
}

class ProtoMessage extends React.Component {
 render() {
   return (
    <ProtoNamespace
      file={this.props.file}
      name={this.props.message.name}
      namespace={this.props.message}
      buildable={true}
      callbacks={this.props.callbacks} />);
  }
}

class ProtoFile extends React.Component {
  render() {
    return <ProtoNamespace
      file={this.props.name}
      name={this.props.name}
      namespace={this.props.contents.root}
      removable={true}
      callbacks={this.props.callbacks} />
  }
}

class ProtoLibrary extends React.Component {
  render() {
    return (
      <React.Fragment>
        <h1>
          Proto library
        </h1>
        <ul>
          {
            this.props.protoFiles.entrySeq().map(
              ([k, v]) =>
                <li key={k}>
                  <ProtoFile
                    name={k}
                    contents={v.get('contents')}
                    callbacks={this.props.callbacks} />
                </li>
            ).toJS()
           }
        </ul>
        <ProtoInput callbacks={this.props.callbacks} />
        <ProtoUpload callbacks={this.props.callbacks} />
      </React.Fragment>
    );
  }
}

class ProtoInput extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isAdding: false
    }
  }

  handleStartAdding() {
    this.setState({isAdding: true});
  }

  handleSubmit() {
    const fileName = this.refs.fileName.value;
    const protoContent = this.refs.protoText.value;
    this.setState({isAdding: false});
    this.props.callbacks.onAddProto(fileName, protoContent);
  }

  render() {
    if (!this.state.isAdding) {
      return <button onClick={() => this.handleStartAdding()} > Add </button>
    }
    return (
      <React.Fragment>
        File name: <input type="text" ref="fileName" /> <br />
        ProtoContent: <br />
        <textarea ref="protoText"/> <br />
        <button onClick={() => this.handleSubmit()} > Submit </button>
      </React.Fragment>
    )
  }
}

class ProtoUpload extends React.Component {
  handleFileChange(event) {
    const file = event.target.files[0];
    const self = this;
    const reader = new FileReader();
    reader.onload = function() {
      const text = reader.result;
      self.props.callbacks.onAddProto(file.name, text);
    };
    reader.onerror = function(err) {
      console.log('FileLoading of', file, 'failed:', err);
    };
    reader.readAsText(file);
  }

  render() {
    return (
      <React.Fragment>
        <button onClick={() => this.refs.fileuploader.click()}>
          From disk
        </button>
        <input
          type="file"
          ref="fileuploader"
          onChange={(event) => this.handleFileChange(event)}
          hidden />
      </React.Fragment>
    );
  }
}

export default ProtoLibrary;
